/**
 * WordPress Dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import {
	Button,
	DropZone,
} from '@wordpress/components';
import { useState, Fragment } from '@wordpress/element';
import { useSelect, useDispatch } from '@wordpress/data';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { uploadMedia } from '@wordpress/media-utils';

const { has } = window._;

const DEFAULT_IMAGE_SIZE = 'full';

const MediaDropZone = ({
	attachmentId = false,
	onUpdate = (attachment) => {
		console.warn('Media DropZone Attachment, use onUpdate prop when using <MediaDropZone/>: ', attachment)
	},
	mediaType = ['image'],
	mediaSize = DEFAULT_IMAGE_SIZE
}) => {
	const [id, setId] = useState(attachmentId);
	const [isUploading, setIsUploading] = useState(false);

	/**
	 * When id (attachmentId) changes update the media and get its src, dimensions, and type.
	 */
	const { media, src, width, height, type } = useSelect(select => {
		const m = id ? select('core').getMedia(id) : false;
		console.log('m...', id, m);
		if ( undefined === m || false === m ) {
			return {
				media: false,
				src: false,
				width: false,
				height: false,
				type: false
			}
		}

		let mediaSourceUrl = false;
		let mediaWidth = false;
		let mediaHeight = false;

		if ( has(  m, [ 'media_details', 'sizes', mediaSize ] ) ) {
			// use mediaSize when available
			mediaWidth = m.media_details.sizes[ mediaSize ].width;
			mediaHeight = m.media_details.sizes[ mediaSize ].height;
			mediaSourceUrl = m.media_details.sizes[ mediaSize ].source_url;
		} else {
			// get fallbackMediaSize if mediaSize is not available
			const fallbackMediaSize = DEFAULT_IMAGE_SIZE;
			if ( has( m, [ 'media_details', 'sizes', fallbackMediaSize ] ) ) {
				// use fallbackMediaSize when mediaSize is not available
				mediaWidth =
					m.media_details.sizes[ fallbackMediaSize ].width;
				mediaHeight =
					m.media_details.sizes[ fallbackMediaSize ].height;
				mediaSourceUrl =
					m.media_details.sizes[ fallbackMediaSize ].source_url;
			} else {
				// use full image size when mediaFallbackSize and mediaSize are not available
				mediaWidth = m.media_details.width;
				mediaHeight = m.media_details.height;
				mediaSourceUrl = m.source_url;
			}
		}

		return {
			media: m,
			src: mediaSourceUrl,
			width: mediaWidth,
			height: mediaHeight,
			type: false !== m ? m.type : false,
		};
	}, [id]);

	const onMediaUpdate = (media) => {
		console.log("onMediaUpdate", id, media.id);
		if ( media.id !== id ) {
			setId(media.id);
			onUpdate(media);
		}
		setIsUploading(false);
	}

	const onDropImage = filesList => {
		console.log('onDropImage', filesList);
		uploadMedia({
			allowedTypes: mediaType,
			filesList,
			onFileChange([image]) {
				console.log('onFileChange', image);
				if (!image.id) {
					console.log('waiting');
					setIsUploading(true);
				} else {
					console.log('found!', image);
					image.sizes = image.media_details.sizes;
					onMediaUpdate(image);
				}
			},
			onError(message) {
				console.error(message);
			},
		});
	};

	return (
		<MediaUploadCheck fallback={__(`Fallback Instructions Should Go Here`)}>
			<MediaUpload
				title={__(`Media Upload`)}
				onSelect={onMediaUpdate}
				allowedTypes={mediaType}
				value={id}
				render={({ open }) => (
					<div>
						{false !== id && false !== media && false !== src && false === isUploading && (
							<div onClick={open} style={{cursor:'pointer'}}>
								<img src={src} width={`${width}px`} height={`${height}px`}/>
							</div>
						)}
						{((false !== id && false === media) || isUploading) && (
							<Button isSecondary isBusy onClick={open}>
								{__(` Loading...`)}
							</Button>
						)}
						{false === id && (
							<Button isSecondary onClick={open}>
								{__(`Set Image`)}
							</Button>
						)}
						<DropZone onFilesDrop={onDropImage} />
					</div>
				)}
			/>
		</MediaUploadCheck>
	);
};

export default MediaDropZone;
